<template>
  <div class="index">
    <transition name="bounce">
      <Header v-show="showHeader"></Header>
    </transition>
    <div class="container">
      <div class="home row l-r">
        <LeftBox
          v-if="isShowLeft&&baseData.isNotPhotoClick"
          @changeLeftShow="changeLeftShow"
        ></LeftBox>
        <router-view />
      </div>
    </div>
    <footer>
      <p>
        Design by
        <a href="/">一世风尘梦离骚</a>
      </p>
    </footer>
    <!-- <a
      href="#"
      class="cd-top cd-is-visible"
    >Top</a> -->
    <Back-top></Back-top>
  </div>
</template>

<script>
import Header from "@/components/common/header/header";
import LeftBox from "@/components/common/left_box/leftBox";
export default {
  components: {
    Header,
    LeftBox,
  },
  computed: {
    // modulePermission() {
    //   return this.$store.getters.getModulePermission;
    // },
  },
  watch: {
    "baseData.isNotPhotoClick": {
      handler: function (newName, oldName) {
        return newName;
      },
      immediate: true,
    },
  },
  data() {
    return {
      scrollTop: "",
      showHeader: true,
      i: 0,
      isShowLeft: true,
    };
  },
  methods: {
    changeLeftShow() {
      if (window.screen.width < 414) {
        this.baseData.isNotPhotoClick = false;
      } else {
        if (
          this.$route.name != "photoDetails" &&
          this.$route.name != "photoOthers"
        ) {
          this.baseData.isNotPhotoClick = true;
        } else {
          this.baseData.isNotPhotoClick = false;
        }
      }
    },
    //滚动条在Y轴上的滚动距离
    getScrollTop() {
      var scrollTop = 0,
        bodyScrollTop = 0,
        documentScrollTop = 0;
      if (document.body) {
        bodyScrollTop = document.body.scrollTop;
      }
      if (document.documentElement) {
        documentScrollTop = document.documentElement.scrollTop;
      }
      scrollTop =
        bodyScrollTop - documentScrollTop > 0
          ? bodyScrollTop
          : documentScrollTop;
      return scrollTop;
    },
    //文档的总高度
    getScrollHeight() {
      var scrollHeight = 0,
        bodyScrollHeight = 0,
        documentScrollHeight = 0;
      if (document.body) {
        bodyScrollHeight = document.body.scrollHeight;
      }
      if (document.documentElement) {
        documentScrollHeight = document.documentElement.scrollHeight;
      }
      scrollHeight =
        bodyScrollHeight - documentScrollHeight > 0
          ? bodyScrollHeight
          : documentScrollHeight;
      return scrollHeight;
    },
    //浏览器视口的高度
    getWindowHeight() {
      var windowHeight = 0;
      if (document.compatMode == "CSS1Compat") {
        windowHeight = document.documentElement.clientHeight;
      } else {
        windowHeight = document.body.clientHeight;
      }
      return windowHeight;
    },
    handleScroll() {
      //判断兼容性**很重要***
      this.scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      // var scrollTop =
      //   window.pageYOffset ||
      //   document.documentElement.scrollTop ||
      //   document.body.scrollTop;
      if (this.scrollTop <= 62) {
        this.showHeader = true;
        $(".cd-top").hide();
      } else {
        this.showHeader = false;
      }
      let scroll = this.scrollTop - this.i;
      this.i = this.scrollTop;

      if (scroll < 0) {
        // console.log("up");
      } else {
        // console.log("down");
      }
      if (
        this.getScrollTop() + this.getWindowHeight() ==
        this.getScrollHeight()
      ) {
        $(".cd-top").show();
      }
    },
  },
  updated() {
    let url = window.localStorage.getItem("url");
    if (window.screen.width < 414) {
      this.baseData.navItem.forEach((element) => {
        if (element.url == url) {
          this.isShowLeft = element.min_modulePermission;
        }
      });
    } else {
      this.baseData.navItem.forEach((element) => {
        if (element.url == url) {
          this.isShowLeft = element.max_modulePermission;
        }
      });
    }
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll, true);
    $(".cd-top").hide();
  },
  destroyed: function () {
    window.removeEventListener("scroll", this.handleScroll, true); //  离开页面清除（移除）滚轮滚动事件
  },
};
</script>

<style lang="scss" src="@/assets/scss/index.scss">
</style>


