<template>
  <!-- 第一种导航 -->
  <div class="box-nav">
    <div class="nav-box-pc">
      <div class="content">
        <!-- logo -->
        <div class="logo">
          <a>
            <span>{{baseData.logo}}</span>
          </a>
        </div>
        <!-- nav -->
        <div class="nav-right">
          <ul class="nav">
            <li class="nav-item" v-for="(i,index) in baseData.navItem" :key="i.id">
              <a @click.prevent="selectItem(index,i.url)">
                <span>{{i.name}}</span>
              </a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    selectItem(index, url) {
      this.baseData.isNotPhotoClick = true;
      $(".nav-item a").removeClass("active");
      $(".nav-item a").eq(index).addClass("active");
      this.$router.push(url);
      if (window.localStorage) {
        window.localStorage.setItem("url", url);
        window.localStorage.setItem("index", index);
      }
    },
  },
  mounted() {
    $(".nav-item a").eq(0).addClass("active");
    if (window.localStorage) {
      let url = window.localStorage.getItem("url");
      let index = window.localStorage.getItem("index");
      if (url && index) {
        this.$router.push(url);
        $(".nav-item a").removeClass("active");
        $(".nav-item a").eq(index).addClass("active");
      }
    }
  },
  destroyed() {
    window.localStorage.removeItem("url");
    window.localStorage.removeItem("index");
  },
};
</script>

<style lang="scss" src="@/assets/scss/header.scss" scoped>
</style>


